<template>
    <v-container class="d-flex justify-space-between flex-column flex-grow-1">
        <div class="mb-10 mt-n4">
            <h6 class="text-h6 pb-4">Kết nối</h6>

            <div class="text-body-1">
                <p>
                    Khởi động lại vào trình nạp khởi động bằng cách giữ đồng thời
                    <strong>nút giảm âm lượng và nút nguồn</strong> cho đến khi thấy
                    dòng chữ
                    <span class="red--text text--darken-3">Fastboot mode</span>,
                    <span class="orange--text text--darken-3">FASTBOOT</span>
                    hoặc tương tự.
                </p>
                <p>
                    Sau khi vào trình nạp khởi động, sử dụng 
                    <strong>cáp USB đi kèm máy hoặc có khả năng truyền dữ liệu.</strong>
                    Tránh dùng hub chuyển đổi.
                </p>
            </div>

            <v-btn
                :color="$root.$data.product === null ? 'primary' : null"
                @click="connect"
                :disabled="connecting"
                >Kết nối</v-btn
            >
        </div>

        <div class="mb-4">
            <connect-banner
                :device="device"
                :connecting="connecting"
                :error="error"
            />
        </div>

        <div class="d-flex justify-space-between flex-row-reverse">
            <v-btn
                color="primary"
                @click="$bubble('nextStep')"
                :disabled="$root.$data.product === null"
                >Tiếp theo <v-icon dark right>mdi-arrow-right</v-icon></v-btn
            >
            <v-btn text @click="$bubble('prevStep')">Quay lại</v-btn>
        </div>
    </v-container>
</template>

<script>
import ConnectBanner from "./ConnectBanner";
import { getDeviceName } from "../core/devices";

export default {
    name: "ConnectStep",

    components: {
        ConnectBanner,
    },

    props: ["device", "blobStore", "active"],

    data: () => ({
        connecting: false,
        error: null,
        firstConnect: true,
    }),

    watch: {
        active: async function (newState) {
            if (newState) {
                this.saEvent("step_connect");
            }
        },
    },

    methods: {
        getDeviceName,

        async errorRetry() {
            await this.connect();
        },

        async connect() {
            this.connecting = true;

            try {
                await this.device.connect();
                this.$root.$data.product = await this.device.getVariable(
                    "product"
                );
                this.error = null;

                if (this.firstConnect) {
                    this.firstConnect = false;
                    this.$bubble("nextStep");
                }

                this.saEvent(`device_connect__${this.$root.$data.product}`);
            } catch (e) {
                let [handled, message] = this.bubbleError(e);
                this.error = message;
                if (!handled) {
                    throw e;
                }
            } finally {
                this.connecting = false;
            }
        },
    },
};
</script>
