<template>
    <div class="d-flex flex-column flex-grow-1">
        <v-stepper
            ref="stepper"
            v-model="curStep"
            :alt-labels="!$vuetify.breakpoint.mobile"
            class="d-flex flex-column flex-grow-1"
            @errorConnectSelect="errorConnectSelect"
            @errorConnectUdev="errorConnectUdev"
            @errorClaim="errorClaim"
            @errorDisconnect="errorDisconnect"
            @errorStorage="errorStorage"
            @errorTimeout="errorTimeout"
            @requestDeviceReconnect="reconnectCallback"
            @prevStep="curStep -= 1"
            @nextStep="curStep += 1"
        >
            <v-stepper-header class="mb-3">
                <v-stepper-step :complete="curStep > 1" step="1">
                    Kết nối
                </v-stepper-step>

                <v-divider></v-divider>

                <v-stepper-step :complete="curStep > 2" step="2">
                    Mở khóa
                </v-stepper-step>

                <v-divider></v-divider>

                <v-stepper-step :complete="curStep > 3" step="3">
                    Tải xuống
                </v-stepper-step>

                <v-divider></v-divider>

                <v-stepper-step :complete="curStep > 4" step="4">
                    Cài đặt
                </v-stepper-step>
            </v-stepper-header>

            <v-stepper-items class="d-flex flex-column flex-grow-1">
                <v-stepper-content
                    step="-1"
                    :class="
                        curStep === -1 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <prepare-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === -1"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="0"
                    :class="
                        curStep === 0 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <install-type-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === 0"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="1"
                    :class="
                        curStep === 1 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <connect-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === 1"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="2"
                    :class="
                        curStep === 2 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <unlock-step
                        :device="device"
                        :blob-store="blobStore"
                        :curStep="curStep"
                        stepNum="2"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="3"
                    :class="
                        curStep === 3 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <download-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === 3"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="4"
                    :class="
                        curStep === 4 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <install-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === 4"
                    />
                </v-stepper-content>

                <v-stepper-content
                    step="5"
                    :class="
                        curStep === 5 ? 'd-flex flex-column flex-grow-1' : null
                    "
                >
                    <finish-step
                        :device="device"
                        :blob-store="blobStore"
                        :active="curStep === 5"
                    />
                </v-stepper-content>
            </v-stepper-items>
        </v-stepper>

        <v-dialog v-model="connectSelectDialog" :width="userAgent.includes('Windows') ? 600 : 500">
            <v-card>
                <v-card-title class="headline">Chưa chọn thiết bị nào</v-card-title>

                <v-card-text>
                    <p>
                        Bạn cần chọn một thiết bị để tiếp tục.
                    </p>
                    <p>
                        Thiết bị không hiện ở đây? Thử các bước sau:
                        <ul class="ml-4 mb-4">
                            <li>Khởi động lại vào trình nạp khởi động</li>
                            <li>Sử dụng cáp USB khác</li>
                            <li>Lau cổng USB</li>
                            <li>Không dùng hub chuyển đổi</li>
                            <li>Kiểm tra lại cáp xem có bị đứt gãy hay lỏng không</li>
                            <li>Vứt máy</li>
                        </ul>
                    </p>
                    <p v-if="userAgent.includes('Windows')">
                        Nếu vẫn không được, thử xem thiết bị Windows của bạn đã cài driver chưa:
                        <ol class="ml-4 mb-4">
                            <li>Cắm dây USB khi điện thoại đã vào trình nạp khởi đông</li>
                            <li>Mở Cài đặt → Windows Update</li>
                            <li>Bấm "Kiểm tra bản cập nhật" và chờ</li>
                            <li>Bấm “Xem các bản cập nhật tùy chỉnh”</li>
                            <li>Chọn “Android Bootloader Interface” (kệ tên hãng đi)</li>
                            <li>Chọn "Tải xuống và cài đặt"</li>
                            <li>Tháo ra cắm lại, không được thì vứt máy</li>
                        </ol>
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryConnectSelect">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="connectUdevDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">Truy cập bị từ chối</v-card-title>

                <v-card-text>
                    <p>
                        Trên các thiết bị chạy Linux, quyền truy cập USB bị giới hạn.
                    </p>
                    <p>
                        Để sửa lỗi này, cấp quyền Android udev cho hệ thống của bạn:
                    </p>

                    <v-list-item two-line>
                        <v-list-item-content>
                            <v-list-item-title>Arch Linux</v-list-item-title>
                            <v-list-item-subtitle
                                >sudo pacman -S
                                android-udev</v-list-item-subtitle
                            >
                        </v-list-item-content>
                    </v-list-item>

                    <v-list-item two-line>
                        <v-list-item-content>
                            <v-list-item-title
                                >Debian, Ubuntu</v-list-item-title
                            >
                            <v-list-item-subtitle
                                >sudo apt install
                                android-sdk-platform-tools-common</v-list-item-subtitle
                            >
                        </v-list-item-content>
                    </v-list-item>

                    <v-list-item two-line>
                        <v-list-item-content>
                            <v-list-item-title
                                >Các distro khác</v-list-item-title
                            >
                            <v-list-item-subtitle
                                >Tự mà tìm</v-list-item-subtitle
                            >
                        </v-list-item-content>
                    </v-list-item>

                    <p>
                        Sau khi đã thiết lập các quyền udev, hãy tháo dây ra cắm lại.
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryConnectUdev">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="claimDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">
                    Không thể giành quyền điều khiển thiết bị
                </v-card-title>

                <v-card-text>
                    <p>
                        Một ứng dụng khác đang điều khiển thiết bị này, và trình cài đặt
                        không thể tương tác.
                    </p>
                    <p>
                        Điều này thường do một tab khác hoặc ứng dụng khác đang sử dụng
                        adb hoặc fastboot.
                    </p>
                    <p>
                        Để sửa lỗi, hãy đóng các tab khác sử dụng tính năng tương tự như
                        Android Flash Tool, hoặc đóng các tab khác sử dụng trình cài đặt này,
                        hoặc nhập lệnh 
                        <strong>adb kill-server</strong>.
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryClaim">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="disconnectDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">
                    Thiết bị đã ngắt kết nối
                </v-card-title>

                <v-card-text>
                    <p>
                        Trình cài đặt không thể tiếp tục do thiết bị
                        đã bị ngắt kết nối.
                    </p>
                    <connect-banner
                        :device="device"
                        :connecting="disconnectReconnecting"
                        :error="disconnectReconnectError"
                    />
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryDisconnect">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="storageDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">Hết dung lượng</v-card-title>

                <v-card-text>
                    <p>
                        Không đủ dung lượng để tiếp tục thực hiện tải xuống và
                        giải nén các tệp cài đặt. Yêu cầu 10GB khả dụng trên thiết bị.
                    </p>
                    <p>
                        Điều này cũng có thể do việc bạn đang sử dụng phiên khách/tab ẩn danh.
                        Do những chế độ này không được cấp nhiều dung lượng, 
                        <strong>hãy chuyển sang tab bình thường.</strong>
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryStorage">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="memoryDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">Hết bộ nhớ</v-card-title>

                <v-card-text>
                    <p>
                        Không đủ bộ nhớ (RAM) để tiếp tục cài đặt
                    </p>
                    <p>
                        Để sửa, hãy
                        <strong>tắt bớt tab sếch</strong> và thử lại.
                    </p>
                    <p>
                        Nếu vẫn không được, vứt máy đi.
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryMemory">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="reconnectDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">Kết nối lại với thiết bị</v-card-title>

                <v-card-text>
                    Để tiếp tục cài đặt, hãy cấp lại quyền điều khiển thiết bị.
                    <v-banner
                        single-line
                        outlined
                        rounded
                        class="mt-8"
                        v-if="reconnectError"
                    >
                        <v-icon slot="icon" color="red darken-3"
                            >mdi-close</v-icon
                        >
                        <div class="my-4">
                            <span
                                class="text-body-1 red--text text--darken-3"
                                >{{ reconnectError }}</span
                            >
                        </div>
                    </v-banner>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="requestReconnect">
                        Kết nối lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>

        <v-dialog v-model="timeoutDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">Kẹt</v-card-title>

                <v-card-text>
                    <p>
                        Kết nối tới thiết bị của bạn đã bị kẹt.
                    </p>
                    <p>
                        Để sửa, sử dụng các nút âm lượng và chọn <strong>“Restart bootloader”</strong> trong menu trình nạp khởi động, sau đó bấm nút 
                        nguồn để khởi động lại.
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryTimeout">
                        Thử  lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<style>
.v-stepper {
    box-shadow: none !important;
    border-radius: 8px !important;
}
.v-stepper__header {
    box-shadow: none !important;
    border-bottom: thin solid rgba(0, 0, 0, 0.12);
}
.v-stepper__wrapper {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
}
</style>

<script>
import * as fastboot from "android-fastboot";
import { BlobStore } from "../core/download";
import ConnectBanner from "./ConnectBanner";
import PrepareStep from "./PrepareStep";
import InstallTypeStep from "./InstallTypeStep";
import ConnectStep from "./ConnectStep";
import UnlockStep from "./UnlockStep";
import DownloadStep from "./DownloadStep";
import InstallStep from "./InstallStep";
import FinishStep from "./FinishStep";

fastboot.setDebugLevel(2);

let device = new fastboot.FastbootDevice();
let blobStore = new BlobStore();

export default {
    name: "Installer",

    components: {
        PrepareStep,
        InstallTypeStep,
        ConnectStep,
        UnlockStep,
        DownloadStep,
        InstallStep,
        FinishStep,
        ConnectBanner,
    },

    data: () => ({
        device: device,
        blobStore: blobStore,
        curStep: -1,
        userAgent: navigator.userAgent,

        connectSelectDialog: false,
        connectUdevDialog: false,
        claimDialog: false,
        storageDialog: false,
        memoryDialog: false,
        timeoutDialog: false,
        retryCallbacks: [],

        disconnectDialog: false,
        disconnectReconnecting: false,
        disconnectReconnectError: null,

        reconnectDialog: false,
        reconnectError: null,
    }),

    methods: {
        handleSelfError(error, retryCallback) {
            this.$refs.stepper.$emit(error, retryCallback);
        },

        errorConnectSelect(retry) {
            this.connectSelectDialog = true;
            this.retryCallbacks.push(retry);
        },
        retryConnectSelect() {
            this.connectSelectDialog = false;
            this.retryCallbacks.pop()();
        },

        errorConnectUdev(retry) {
            this.connectUdevDialog = true;
            this.retryCallbacks.push(retry);
        },
        retryConnectUdev() {
            this.connectUdevDialog = false;
            this.retryCallbacks.pop()();
        },

        errorClaim(retry) {
            this.claimDialog = true;
            this.retryCallbacks.push(retry);
        },
        retryClaim() {
            this.claimDialog = false;
            this.retryCallbacks.pop()();
        },

        errorDisconnect(retry) {
            this.$root.$data.product = null;

            this.disconnectReconnecting = false;
            this.disconnectReconnectError = null;
            this.disconnectDialog = true;
            this.retryCallbacks.push(retry);
        },
        async retryDisconnect() {
            this.disconnectReconnecting = true;
            this.$root.$data.product = null;

            try {
                await this.device.connect();
                this.$root.$data.product = await this.device.getVariable(
                    "product"
                );
                this.disconnectReconnectError = null;
            } catch (e) {
                let [handled, message] = this.bubbleError(e, this.retryDisconnect);
                this.disconnectReconnectError = message;
                if (!handled) {
                    throw e;
                }

                this.disconnectReconnecting = false;
                return;
            }

            this.disconnectReconnecting = false;
            this.disconnectDialog = false;
            this.retryCallbacks.pop()();
        },

        errorStorage(retry) {
            this.storageDialog = true;
            this.retryCallbacks.push(retry);
        },
        retryStorage() {
            this.storageDialog = false;
            this.retryCallbacks.pop()();
        },

        errorMemory(retry) {
            this.memoryDialog = true;
            this.retryCallbacks.push(retry);

            this.saEvent("error__out_of_memory");
        },
        retryMemory() {
            this.memoryDialog = false;
            this.retryCallbacks.pop()();
        },

        reconnectCallback() {
            this.reconnectError = null;
            this.reconnectDialog = true;
        },
        async requestReconnect() {
            try {
                await this.device.connect();
                this.reconnectDialog = false;
                this.reconnectError = null;
            } catch (e) {
                this.reconnectError = e.message;
            }
        },

        errorTimeout(retry) {
            this.timeoutDialog = true;
            this.retryCallbacks.push(retry);
        },
        retryTimeout() {
            this.timeoutDialog = false;
            this.retryCallbacks.pop()();
        },
    },
};
</script>
