<template>
    <v-container class="d-flex justify-space-between flex-column flex-grow-1">
        <div class="mt-n4">
            <h6 class="text-h6 pb-4">Mở khóa trình nạp khởi động</h6>

            <div class="text-body-1">
                <p>
                    Để cài đặt ROM khác lên thiết bị của bạn, bạn cần mở
                    khóa trình nạp khởi động của thiết bị.
                </p>
                <p>
                    Nếu đây là lần đầu bạn làm điều này, hãy làm các bước sau 
                    (tùy thuộc theo thiết bị, các bước sau dành cho các thiết bị Pixel):
                </p>
                <ol class="ml-4 mb-8">
                    <li>Khởi động lại vào hệ thống Android</li>
                    <li>
                        Vào Cài đặt → “Giới thiệu về điện thoại” và lướt xuống dưới cùng
                    </li>
                    <li>
                        Chạm “Số bản dựng” liên tục cho đến khi Tùy chọn nhà phát triển 
                        được kích hoạt
                    </li>
                    <li>
                        Vào Cài đặt → Hệ thống → Tùy chọn nhà phát triển
                    </li>
                    <li>Bật “Mở khóa OEM”</li>
                    <li>Khởi động lại vào trình nạp khởi động</li>
                </ol>
            </div>

            <v-btn
                color="primary"
                @click="unlock"
                :disabled="unlocking || unlocked"
                >Mở khóa</v-btn
            >
        </div>

        <div class="mb-4">
            <v-banner single-line outlined rounded class="mt-4" v-if="unlocked">
                <v-icon slot="icon" color="green darken-3">mdi-check</v-icon>
                <span class="text-body-1 green--text text--darken-3"
                    >Đã mở khóa trình nạp khởi động</span
                >
            </v-banner>
            <v-banner
                single-line
                outlined
                rounded
                class="mt-4"
                v-else-if="unlocking"
            >
                <v-progress-circular
                    slot="icon"
                    indeterminate
                    color="primary"
                ></v-progress-circular>
                <span class="text-body-1"
                    >Đang mở khóa trình nạp khởi động, hãy xác nhận trên thiết bị
                    bằng nút tăng/giảm âm lượng.</span
                >
            </v-banner>
            <v-banner
                single-line
                outlined
                rounded
                class="mt-4"
                v-else-if="error"
            >
                <v-icon slot="icon" color="red darken-3">mdi-close</v-icon>
                <span class="text-body-1 red--text text--darken-3">{{
                    error
                }}</span>
            </v-banner>
        </div>

        <div class="d-flex justify-space-between flex-row-reverse">
            <v-btn
                color="primary"
                @click="$bubble('nextStep')"
                :disabled="!unlocked"
                >Tiếp tục <v-icon dark right>mdi-arrow-right</v-icon></v-btn
            >
            <v-btn text @click="$bubble('prevStep')">quay lại</v-btn>
        </div>

        <v-dialog v-model="oemUnlockDialog" width="500" persistent>
            <v-card>
                <v-card-title class="headline">
                    Kích hoạt Mở khóa OEM
                </v-card-title>

                <v-card-text>
                    <p>
                        Vì lý do bảo mật, việc mở khóa trình khởi động bị vô hiệu hóa
                        theo mặc đình. Bạn cần kích hoạt Mở khóa OEM bằng các bước sau:
                    </p>

                    <ol class="ml-4 mb-4">
                        <li>Khởi động lại vào hệ thống Android</li>
                        <li>
                            Vào Cài đặt → “Giới thiệu về điện thoại” và lướt xuống dưới cùng
                        </li>
                        <li>
                            Chạm “Số bản dựng” liên tục cho đến khi Tùy chọn nhà phát triển 
                            được kích hoạt
                        </li>
                        <li>
                            Vào Cài đặt → Hệ thống → Tùy chọn nhà phát triển
                        </li>
                        <li>Bật “Mở khóa OEM”</li>
                        <li>Khởi động lại vào trình nạp khởi động</li>
                    </ol>

                    <p>
                        Sau khi đã Mở khóa OEM, hãy thử mở khóa lại trình nạp khởi động.
                    </p>
                </v-card-text>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="primary" text @click="retryOemUnlock">
                        Thử lại
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<script>
import { FastbootError } from "android-fastboot";

export default {
    name: "UnlockStep",

    props: ["device", "blobStore", "curStep", "stepNum"],

    data: () => ({
        unlocking: false,
        unlocked: undefined,
        initialUnlocked: undefined,
        firstUnlock: true,
        error: null,
        oemUnlockDialog: false,
    }),

    watch: {
        curStep: async function (newStep, oldStep) {
            if (newStep == this.stepNum) {
                this.saEvent("step_unlock");

                try {
                    // Get unlock state once and save it. Not all bootloaders
                    // update the unlocked value immediately after unlocking.
                    if (this.unlocked === undefined) {
                        this.unlocked =
                            (await this.device.getVariable("unlocked")) ===
                            "yes";
                        this.initialUnlocked = this.unlocked;
                    }

                    // Skip step only if unlock state was never changed
                    if (this.unlocked && this.initialUnlocked) {
                        if (newStep > oldStep) {
                            this.$bubble("nextStep");
                        } else {
                            this.$bubble("prevStep");
                        }
                    }

                    this.error = null;
                } catch (e) {
                    let [handled, message] = this.bubbleError(e);
                    this.error = message;
                    if (!handled) {
                        throw e;
                    }
                }
            }
        },
    },

    methods: {
        async retryOemUnlock() {
            this.oemUnlockDialog = false;
            await this.unlock();
        },

        async errorRetry() {
            await this.unlock();
        },

        async unlock() {
            this.unlocking = true;

            try {
                if (!this.device.isConnected) {
                    await this.device.connect();
                }

                // Unlocking can't be done in fastbootd
                if ((await this.device.getVariable("is-userspace")) === "yes") {
                    await this.device.reboot("bootloader", true, () => {
                        this.$bubble("requestDeviceReconnect");
                    });
                }

                await this.device.runCommand("flashing unlock");
            } catch (e) {
                this.unlocking = false;

                if (e instanceof FastbootError && e.status === "FAIL") {
                    if (e.message.includes("already")) {
                        /* Already unlocked = success */
                        return;
                    } else if (e.message.includes("canceled")) {
                        this.error = "Unlock request was canceled";
                        return;
                    } else if (e.message.includes("not allowed")) {
                        this.error = "OEM unlocking is not enabled";
                        this.oemUnlockDialog = true;
                        return;
                    }
                }

                let [handled, message] = this.bubbleError(e);
                this.error = message;
                if (!handled) {
                    throw e;
                }
            }

            this.unlocked = true;
            this.error = null;

            if (this.firstUnlock) {
                this.firstUnlock = false;
                this.$bubble("nextStep");
            }

            this.unlocking = false;
            this.saEvent(`unlock_bootloader__${this.$root.$data.product}`);
        },
    },
};
</script>
